import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { CommonService } from 'src/app/core/services/common.service';
import { PartyService } from 'src/app/core/services/party.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { WarehouseService } from 'src/app/core/services/warehouse.service';

@Component({
  selector: 'app-edit-shipping',
  templateUrl: './edit-shipping.component.html',
  styleUrls: ['./edit-shipping.component.scss']
})
export class EditShippingComponent implements OnInit {

  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  public shippingForm: any;
  public submitted: boolean = false;
  public today: Date;
  countryList: any[];
  packageData: any;
  packStatus: any;
  shippingCharge: any;
  subTotal: any;

  constructor(private formBuilder: FormBuilder,
    public router: Router, private commonService: CommonService,
    private toastService: ToastNotificationService, public dialog: MatDialog,
    private titleCasePipe: TitleCasePipe,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<EditShippingComponent>,
    private warehouseService: WarehouseService,
    private partyService: PartyService
  ) {
    this.today = new Date();
    this.packageData = this.data.packageData
    this.packStatus = this.data.packageData?.packStatus
  }

  async ngOnInit(): Promise<void> {
    try {
      this.shippingForm = this.formBuilder.group({
          name: [{value: null, disabled: true }, Validators.required],
          saleInvoice: [{value: null, disabled: true}, Validators.required],
          trackingDetails: [null],
          shipmentId: [null],
          carrier: [null],
          packStatus: [null]
      });
      await this.getPackageData()
    } catch (error) {
        this.toastService.toastMsg({
          title: 'Error',
          content: error
        })
    }
  }  
  
  get f() {
    return this.shippingForm.controls;
  }

  cancel() {
    this.dialogRef.close();
  }

  async getPackageData(){
    if(this.packageData){
      await this.shippingForm.patchValue({
        name: this.packageData.customerName,
        saleInvoice: this.packageData.invoiceNo,
        trackingDetails: this.packageData.trackingDetails,
        shipmentId: this.packageData.shipmentId,
        carrier: this.packageData.carrier,
        packStatus: this.packageData.packStatus,
      })
      this.subTotal = this.packageData?.totalAmount
      this.shippingCharge = this.packageData?.shippingCharge
    }
  }

  numberOnly(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  numberDecimalOnly(event): boolean {
    var charCode = (event.which) ? event.which : event.keyCode;
    if (charCode != 46 && charCode > 31
      && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  onSave() {
    this.submitted = true;
    if (this.shippingForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      return false;
    } else {
      const data = {
        userId: this.currentUser.id,
        updateData: {
          name: "package",
          customerName: this.packageData?.customerName,
          mobile: this.packageData?.mobile,
          address: this.packageData?.address,
          trackingDetails: this.shippingForm.value.trackingDetails,
          shipmentId: this.shippingForm.value.shipmentId,
          carrier: this.shippingForm.value.carrier,
          packStatus: (this.packStatus === 'PACKED' ? 'SHIPPED' : (this.packStatus === 'SHIPPED' ? 'DELIVERED' : this.packStatus)),
        }
      }
      this.warehouseService.updatePackage(data, this.packageData.id).then( res => {
        if(res.success) {
          this.dialogRef.close();
          this.commonService.notifyDataAdded()
          this.toastService.toastMsg({
            title: "Success",
            content: "Shipping Data Updated Successfully!!!",
          });
        }
      })
    }
  }

}
